/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import tradingData from './data-bb-trading-http';

const tradingDataModuleKey = 'data-bb-trading-http-ng';

export const tradingDataKey = 'data-bb-trading-http-ng:tradingData';

export default ng
  .module(tradingDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(tradingDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       tradingData(config),
      ],
    };
  }])
  .name;



